/**
 * Method applies linear transformation by passed x, y and angle and return new coordinates
 * @param {Number} x - coordinate
 * @param {Number} y - coordinate
 * @param {Number} angle - transformation angle
 * @return {{x: Number, y: Number}}
 */
const coordinatesOnAngle = (x, y, angle) => {
  // Linear transformation for coordinates
  //  x2     x      cos(angle)   sin(angle) 
  //      =     *                           
  //  y2     y     -sin(angle)   cos(angle) 

  let angleInRad = angle * (Math.PI / 180);

  return {
    x: x * Math.cos(angleInRad) + y * Math.sin(angleInRad),
    y: -x * Math.sin(angleInRad) + y * Math.cos(angleInRad)
  };
};

export {
  coordinatesOnAngle
};
